.cost-item__date{
    margin-left: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: rgb(158, 211, 211);
    border: 2px solid white;
    width: 35%;
    padding: 10px 20px;
    transition: all ease .1s;
}
.cost-item__date:hover{
    border: 2px solid #4082b8;
}
.cost-item__date .month{
    font-size: 16px;
    font-weight: 600;
}