.cost-item{
    width: 90%;
    max-width: 98%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px;
    margin: 16px 0;
    background-color: #627bea;
    margin: 10px 30px;
    border: 3px solid transparent;
    transition: all ease .3s;
    color: white;
    font-weight: 600;
}
.cost-item:hover{
    background-color: rgb(207, 243, 243);
    color: #4082b8 !important;
    box-shadow: 0 0 10px #4082b8;
    cursor: pointer;
}

.cost-item__description{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    flex-flow: column-reverse;
    gap: 16px;
    justify-content: flex-start;
    flex: 1;
}
.cost-item__description h2{
    font-size: 20px;
    flex: 1;
    margin: 0 8px;
}

.cost-item__price{
    font-size: 16px;
    font-weight: bold;
    color: #fefae1;
    background-color: #4082b8;
    border: 2px solid white;
    padding: 8px;
    border-radius: 12px;
    cursor: pointer;
    transition: all ease 1s;
}
.cost-item__price:hover{
    color: #4082b8;
    background-color: transparent;
    border: 2px solid #4082b8;
}

.change-button{
    color: #4082b8;
    background: #fefae1;
    padding: 8px 16px;
    margin-left: 20px;
    border: 2px solid #fefae1;
    border-radius: 25px;
    cursor: pointer;
    font-weight: 600;
    font-size: 12px;
    transition: all ease .2s;
}
.change-button:hover{
    color: #fefae1;
    background: #4082b8;
    border: 2px solid #4082b8;
}