.button-add-cost-form{
    padding: 10px 24px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    transition: all ease .1s;
    color: #4082b8;
    margin-top: 16px;
    font-size: 18px;
    font-weight: 600;
}
.button-add-cost-form:hover{
    transform: scale(1.03);
}

.new-cost__controls{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 16px;
    margin-bottom: 16px;
    text-align: center;
}
.new-cost__control label{
    font-weight: 600;
    margin-bottom: 8px;
    display: block;
    color: #4082b8;
    font-size: 24px;
}
.new-cost__control input{
    font: inherit;
    padding: 8px 12px;
    border-radius: 5px;
    border: 1px solid #ccc;
    width: 320px;
    max-width: 100%;
    color: #4082b8;
    font-size: 20px;
    font-weight: 600;
    outline: none;
}
.new-cost__actions{
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: center;
}
.new-cost__actions button{
    padding: 10px 24px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    transition: all ease .1s;
    color: #4082b8;
    margin-right: 10px;
    font-size: 18px;
    font-weight: 600;
}
.new-cost__actions button:hover{
    transform: scale(1.03);
}