.costs-filter{
    width: 96%;
    border-radius: 26px;
    background: #627bea;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10px 0;
    transition: all ease .2s;
}
.costs-filter:hover{
    background: #87e6d5;
    box-shadow: 0 0 20px #627bea;
    cursor: pointer;
}

.choose-year{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    color: white;
    padding: 20px;
    font-weight: 600;
    font-size: 20px;
}
.choose-year select{
    width: 30%;
    background: #627bea;
    border: 2px solid white;
    border-radius: 20px;
    padding: 5px 10px;
    color: white;
    outline: none;
    transition: all ease .2s;
    cursor: pointer;
}
.choose-year select:hover{
    background: #87e6d5;
}
.choose-year select option{
    background: #87e6d5;
    transition: all ease .2s;
    cursor: pointer;
}

