.diagramm-bar{
    width: 4%;
    height: 100px;
}

.diagramm-bar__inner{
    height: 100%;
    width: 100%;
    border: 2px solid #313131;
    border-radius: 12px;
    background: white;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}
.diagramm-bar__progress-line{
    width: 100%;
    transition: all ease .2s;
    background-color: green;
}
.diagramm-bar__progress-line:hover{
    background-color: #627bea;
}
.diagramm-bar__title{
    font-weight: 600;
    font-size: 14px;
    color: #313131;
    text-align: center;
}